import {
  setTokenStorage,
  getTokenStorage,
  configHandle
} from '@/test/tool.js'
export default {
	data() {
		return {
			userInfo:{
				username:"",
				password:""
			}
		};
	},
	components:{
		
	},
	mounted() {
		
	},
	methods:{
		jumpRegster(){
			uni.redirectTo({
			    url: '../register/register',
				
			});
		},
		loginJump(){
			var that=this;
			console.log(this.userInfo);
			if(this.userInfo.username.length<1){
				uni.showModal({
					 // title: "登录提示",
					content: "请输入账号!",
					showCancel: false,
					confirmText: "确定"
				})
				return
			};
			if(this.userInfo.password.length<1){
				uni.showModal({
					 // title: "登录提示",
					content: "请输入密码!",
					showCancel: false,
					confirmText: "确定"
				})
				return
			};
			uni.showLoading({
				title: 'loading'
			});
			//发起请求
			this.$http.post('/interface/rest/http/xlwb/xlgc-wb-jdh-yhdl.htm',that.userInfo).then(res => {
				uni.hideLoading();
				console.log(res.data);
				if(res.data.msgState==1){
					
					//设置token
					 setTokenStorage(res.data.list[0].token) // todo 储存token，可更换为自己的储存token逻辑
					uni.redirectTo({
					    url: '../index/index?show_index=0',
						
					});
					
				}
			}).catch(err => {
				console.log(err);
				uni.hideLoading();
			})
			//TODO
			// uni.redirectTo({
			//     url: '../index/index',
				
			// });
		}
	}
		
	

}

